import { useEffect, useState } from "react";
import useRoute from "../../hooks/useRoute.js";
import { styles, topBar } from "./styles.module.css";
import { primaryBtn } from "../../styles/buttons.module.css";
import { getChampions, getTop } from "../../context/championProvider.js";
import { useDebounce } from "../../hooks/useDebounce.js";
import { Link } from "react-router-dom";
import { useSearch } from "../../context/searchProvider.js";
import Loading from "../../Loading";

function ChampionList() {
  const { search } = useSearch();
  const [champions, setChampions] = useState([]);
  const [loading, setLoading] = useState(true);
  const debouncedSearch = useDebounce(search, 300);
  const route = useRoute();

  useEffect(() => {
    setLoading(true);
    async function fetchAll() {
      setChampions(await getChampions(route, debouncedSearch));
      setLoading(false);
    }
    async function fetchTop() {
      setChampions(await getTop(debouncedSearch));
      setLoading(false);
    }
    if (route === "") {
      fetchTop();
    } else {
      fetchAll();
    }
  }, [route, debouncedSearch]);

  return (
    <div className={styles}>
      <div className={topBar}>
        <h1>
          <span>{route === "" ? "Top" : route}</span> Champions
        </h1>
        {route !== "" && (
          <Link to={`/${route}/add`} className={primaryBtn}>
            Add Champion
          </Link>
        )}
      </div>
      {loading ? (
        <Loading />
      ) : champions?.length ? (
        <ul>
          {champions.map(({ name, id, city }) => (
            <li key={name}>
              <Link to={`/${city}/${id}`}>{name}</Link>
            </li>
          ))}
        </ul>
      ) : (
        "No champions found"
      )}
    </div>
  );
}

export default ChampionList;
