import { useState, useRef, useEffect } from "react";
import useRoute from "../hooks/useRoute.js";
import { addChampion } from "../context/championProvider.js";
import { styles, input, buttons } from "./styles.module.css";
import { primaryBtn, lightBtn } from "../styles/buttons.module.css";

function AddChampion() {
  const route = useRoute();
  const [name, setName] = useState("");

  function handleName(e) {
    setName(e.target.value);
  }

  function handleSave(e) {
    e.preventDefault();
    addChampion(route, name);
    handleGoBack();
  }

  function handleGoBack() {
    history.back();
  }

  const focusRef = useRef(null);

  useEffect(() => {
    focusRef.current.focus();
  }, []);

  return (
    <div className={styles}>
      <h1>
        <span>{route}</span> Champion
      </h1>
      <form onSubmit={handleSave}>
        <label htmlFor="name">Name</label>
        <input
          value={name}
          name="name"
          id="name"
          onChange={handleName}
          className={input}
          ref={focusRef}
        />
        <div className={buttons}>
          <button type="submit" disabled={!name} className={primaryBtn}>
            Save
          </button>
          <button onClick={handleGoBack} className={lightBtn} type="button">
            Cancel
          </button>
        </div>
      </form>
    </div>
  );
}

export default AddChampion;
