import { useEffect, useState } from "react";
import useRoute from "../hooks/useRoute.js";
import { useParams } from "react-router-dom";
import {
  getChampion,
  updateChampion,
  deleteChampion,
} from "../context/championProvider.js";
import { styles, input, buttons } from "./styles.module.css";
import { primaryBtn, lightBtn, dangerBtn } from "../styles/buttons.module.css";
import Loading from "../Loading";

function ChampionDetail() {
  const { id } = useParams();
  const route = useRoute();
  const [name, setName] = useState("");
  const [loading, setLoading] = useState(true);

  function handleName(e) {
    setName(e.target.value);
  }

  function handleSave(e) {
    e.preventDefault();
    updateChampion(route, id, name);
    handleGoBack();
  }

  function handleDelete() {
    deleteChampion(route, id);
    handleGoBack();
  }

  function handleGoBack() {
    history.back();
  }

  useEffect(() => {
    async function getName() {
      const { name } = await getChampion(route, id);
      setName(name);
      setLoading(false);
    }

    getName();
  }, []);

  return (
    <div className={styles}>
      {loading ? (
        <Loading />
      ) : (
        <div>
          <h1>
            <span>{name}</span> Details
          </h1>
          <form onSubmit={handleSave}>
            <label htmlFor="id">ID</label>
            <input value={id} name="id" id="id" className={input} readOnly />
            <label htmlFor="name">Name</label>
            <input
              value={name}
              name="name"
              id="name"
              onChange={handleName}
              className={input}
            />
            <div className={buttons}>
              <button type="submit" disabled={!name} className={primaryBtn}>
                Save
              </button>
              <button onClick={handleGoBack} className={lightBtn} type="button">
                Cancel
              </button>
              <button
                onClick={handleDelete}
                className={dangerBtn}
                type="button"
              >
                Delete
              </button>
            </div>
          </form>
        </div>
      )}
    </div>
  );
}

export default ChampionDetail;
