import { useState, useRef } from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Champions from "../Champions";
import About from "../About";
import ChampionDetail from "../ChampionDetail";
import { styles, menuBtn } from "./styles.module.css";
import AddChampion from "../AddChampion";
import Nav from "../Nav";
import { ProvideSearch } from "../context/searchProvider.js";
import ScrollToTop from "../ScrollToTop.js";
import classNames from "classnames";

function App() {
  const [menu, setMenu] = useState("menu-closed");

  function handleMenu() {
    if (menu === "menu-closed") {
      setMenu("menu-open");
    } else {
      setMenu("menu-closing");
      setTimeout(() => {
        setMenu("menu-closed");
      }, 600);
    }
  }

  const scrollRef = useRef(null);

  return (
    <ProvideSearch>
      <div className={classNames(styles, menu)} ref={scrollRef} tabIndex="-1">
        <div className={menuBtn}>
          <button onClick={handleMenu}>
            <span className="material-icons">menu</span>
          </button>
        </div>
        <BrowserRouter
          basename={
            process.env.NODE_ENV === "development"
              ? "/"
              : "/battle-of-champions"
          }
        >
          <ScrollToTop {...{ scrollRef }} />
          <Nav {...{ handleMenu }} />
          <main>
            <Routes>
              <Route path="/sparta/add" element={<AddChampion />} />
              <Route path="/sparta/:id" element={<ChampionDetail />} />
              <Route path="/sparta" element={<Champions />} />
              <Route path="/argos/add" element={<AddChampion />} />
              <Route path="/argos/:id" element={<ChampionDetail />} />
              <Route path="/argos" element={<Champions />} />
              <Route path="/about" element={<About />} />
              <Route path="/" element={<Champions />} />
            </Routes>
          </main>
        </BrowserRouter>
      </div>
    </ProvideSearch>
  );
}

export default App;
