import { useSearch } from "../context/searchProvider.js";
import { useRef } from "react";
import { styles, input, clear } from "./styles.module.css";

function ChampionSearch() {
  const { search, setSearch } = useSearch();

  function handleSearch(e) {
    setSearch(e.target.value);
  }

  function clearSearch() {
    setSearch("");
    searchRef.current.focus();
  }

  function selectSearch() {
    searchRef.current.select();
  }

  const searchRef = useRef(null);

  return (
    <div className={styles} onClick={selectSearch}>
      <span className="material-icons search">search</span>
      <input
        value={search}
        onChange={handleSearch}
        placeholder="Search"
        ref={searchRef}
        className={input}
      />
      {search && (
        <button className={clear} onClick={clearSearch}>
          <span className="material-icons clear">clear</span>
        </button>
      )}
    </div>
  );
}

export default ChampionSearch;
