import { Link } from "react-router-dom";
import useRoute from "../hooks/useRoute.js";
import { styles, navContent, appTitle, cancelBtn } from "./styles.module.css";

function Nav({ handleMenu }) {
  const route = useRoute();

  function handleClick() {
    if (window.matchMedia("(max-width: 715px)").matches) handleMenu();
  }

  return (
    <div className={[styles, route].join(" ")}>
      <div className={navContent}>
        <div className={appTitle}>Battle of Champions</div>
        <nav>
          <ul>
            <li>
              <Link
                to="/"
                className={route === "" ? "active" : ""}
                onClick={handleClick}
              >
                Dashboard
              </Link>
            </li>
            <li>
              <Link
                to="/sparta"
                className={route === "sparta" ? "active" : ""}
                onClick={handleClick}
              >
                Sparta Champions
              </Link>
            </li>
            <li>
              <Link
                to="/argos"
                className={route === "argos" ? "active" : ""}
                onClick={handleClick}
              >
                Argos Champions
              </Link>
            </li>
            <li>
              <Link
                to="/about"
                className={route === "about" ? "active" : ""}
                onClick={handleClick}
              >
                About
              </Link>
            </li>
          </ul>
          <button tabIndex="0" className={cancelBtn} onClick={handleMenu}>
            <span className="material-icons">cancel</span>
          </button>
        </nav>
      </div>
    </div>
  );
}

export default Nav;
